//vuex，提供全局数据存储
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    modulePermission:true,////模块的权限
  },
  getters:{
    //模块的权限
    getModulePermission:state => {
      return state.modulePermission;
    },
  },
  //将vue组件中的数据赋值给state中的数据
  mutations:{
    //此函数默认传两个参数，第一个是state对象，第二个是我们填入的数据
    setModulePermission:(state,status) => {
      state.modulePermission = status;
    },
  }
})



