//此页面配置路由
const routers = [{
    path: "/",
    name: "index",
    meta: {
        title: "生活的理想，就是为了理想的生活"
    },
    component: resolve => require(["@/components/index"], resolve),
    redirect: '/home',
    children: [{
            path: "/home",
            name: "home",
            meta: {
                title: "生活的理想，就是为了理想的生活"
            },
            component: resolve => require(["@/components/pages/home/home"], resolve)
        },
        {
            path: "/article",
            name: "article",
            meta: {
                title: "文章"
            },
            component: resolve => require(["@/components/pages/article/article"], resolve),
            children: [

            ]
        },
        {
            path: "/details",
            name: "details",
            meta: {
                title: "文章详情页"
            },
            component: resolve => require(["@/components/pages/article/details"], resolve)
        },
        {
            path: "/photo",
            name: "photo",
            meta: {
                title: "相册"
            },
            component: resolve => require(["@/components/pages/photo/photo"], resolve)
        },
        {
            path: "/photoDetails",
            name: "photoDetails",
            meta: {
                title: "相册详情"
            },
            component: resolve => require(["@/components/pages/photo/photoDetails"], resolve)
        },
        {
            path: "/message",
            name: "message",
            meta: {
                title: "留言"
            },
            component: resolve => require(["@/components/pages/message/message"], resolve)
        },
        {
            path: "/about",
            name: "about",
            meta: {
                title: "关于"
            },
            component: resolve => require(["@/components/pages/about/about"], resolve)
        }
    ]
}];

export default routers;


